.owl-carousel {
	.owl-stage {
		display: flex;
	}

	.owl-item {
		height: auto;

		.owl-item-container {
			height: 100%;
			display: flex;
			flex-direction: column;

			p:not(:has(.btn)) { margin-bottom: 0; }

			p:has(.btn) { margin-top: auto; }

			.btn { width: 100%; }
		}
	}
}

#start section.modular-text:nth-of-type(1) {
	margin-top: 4rem;
}
